<template><section><h1>Hamburger Button</h1>
<p>A simple, accessible hamburger button component.</p>
<h3>Example</h3>
<component-demo ref="component-demo"/>
<pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">hamburger-button</span> <span class="hljs-attr">:active</span>=<span class="hljs-string">"expanded"</span> @<span class="hljs-attr">update:active</span>=<span class="hljs-string">"value =&gt; expanded = value"</span> <span class="hljs-attr">:barStyle</span>=<span class="hljs-string">"barStyle"</span>/&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data() {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">expanded</span>: <span class="hljs-literal">false</span>,
      <span class="hljs-attr">barStyle</span>: {
        <span class="hljs-attr">backgroundColor</span>: <span class="hljs-string">'red'</span>
      }
    }
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
<span class="hljs-comment">&lt;!-- component-demo.vue --&gt;</span>
</code></pre>
<h3>Props</h3>
<table>
<thead>
<tr>
<th>Name</th>
<th>Type</th>
<th>Default</th>
<th>Required</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td><code v-pre>active</code></td>
<td>Boolean</td>
<td>false</td>
<td>true</td>
<td>Determines if the button is in the active state</td>
</tr>
<tr>
<td><code v-pre>barStyle</code></td>
<td>Object</td>
<td></td>
<td>false</td>
<td>Style declarations to be applied to each bar</td>
</tr>
</tbody>
</table>
<h3>Events</h3>
<table>
<thead>
<tr>
<th>Name</th>
<th>Params</th>
<th>Description</th>
</tr>
</thead>
<tbody>
<tr>
<td><code v-pre>update:active</code></td>
<td>active</td>
<td>Fired after the button is clicked, with new active state</td>
</tr>
</tbody>
</table>
</section></template><script>

export default {components:{'component-demo':{"template":"\n  <hamburger-button :active=\"expanded\" @update:active=\"value => expanded = value\" :barStyle=\"barStyle\"/>\n",
  data() {
    return {
      expanded: false,
      barStyle: {
        backgroundColor: 'red'
      }
    }
  }
}}}</script>


