<template><section><h1>Quick Start</h1>
<h2>Install (without npm)</h2>
<p>Clone the Git repository of this library and link it.</p>
<pre v-pre><code v-pre class="language-bash">git <span class="hljs-built_in">clone</span> https://github.com/mhgbrown/vue-hamburger-button.git
<span class="hljs-built_in">cd</span> vue-hamburger-button
npm install
npm run build
npm link
</code></pre>
<p>In your client project root folder:</p>
<pre v-pre><code v-pre class="language-bash">npm link vue-hamburger-button
</code></pre>
<h2>Use the library</h2>
<pre v-pre><code v-pre class="language-javascript"><span class="hljs-keyword">import</span> Vue <span class="hljs-keyword">from</span> <span class="hljs-string">'vue'</span>
<span class="hljs-keyword">import</span> * <span class="hljs-keyword">as</span> VueHamburgerButton <span class="hljs-keyword">from</span> <span class="hljs-string">'vue-hamburger-button'</span>

Vue.use(VueHamburgerButton);
</code></pre>
<h3>No Conflict</h3>
<p>All components will be installed with no prefix by default, you can add any prefix
to them to avoid conflicts with other libs if needed.</p>
<p>For example:</p>
<pre v-pre><code v-pre class="language-javascript">Vue.use(VueHamburgerButton, { <span class="hljs-attr">prefix</span>: <span class="hljs-string">'abc'</span> });
</code></pre>
<p>Results in:</p>
<ul>
<li>Components such as <code v-pre>&lt;hamburger-button&gt;</code> becomes <code v-pre>&lt;abc-hamburger-button&gt;</code></li>
</ul>
</section></template><script>export default {components:{}}</script>


